export default {
	data() {
		return {
			imgUrl: 'http://106.15.194.58/images/', //图片接口
			orderId: ''
		}
	},
	onLoad(e) {
		this.orderId = e.id
	},
	methods: {
		toOrder(){
			uni.switchTab({
				url: '../order/order'
			})
		},
		toAssessOrder(){
			uni.navigateTo({
				url:'../assessOrder/assessOrder?id='+this.orderId,
				animationDuration:200,
				animationType:'slide-in-right'
			})
		}
	}
}
